    margin-bottom: 2rem;
    color: #fff;
}

h3 {
    font-size: 2rem;
    text-transform: uppercase;
    margin-bottom: 2rem;
    color: rgba(32,78,147,255);
}

input {
    appearance: none;
    border: none;
    outline: none;
    background: none;

    display: block;
    width: 100%;
    max-width: 400px;
    margin: 0 auto;
    font-size: 1.5rem;
    margin-bottom: 2rem;
    padding: 0.5rem 0rem;
}

input:not([type="submit"]) {
    opacity: 0.8;
    transition: 0.4s;
}

input:focus:not([type="submit"]) {
    opacity: 1;
}

input::placeholder {
    color: inherit;
}

form.register input:not([type="submit"]) {
    color: #fff;
    border-bottom: 2px solid #fff;
}

